.main-genre-pagination {
	grid-column: 2/2;
	color: white;
	width: fit-content;
	margin: 20px auto;
	height: fit-content;
	.numbers {
		width: 100%;
		display: flex;
		gap: 2px;
		justify-content: space-around;
		cursor: pointer;
		.arrow {
			width: 30px;
			fill: white;
			display: flex;
			justify-content: center;
			align-items: center;
			transition: 0.1s ease-in;

			svg {
				width: 15px;
				height: 15px;
			}
			&:hover {
				background-color: rgba(255, 255, 255, 0.296);
			}
		}
		.pagination-page {
			min-width: 30px;
			height: 30px;
			display: flex;
			justify-content: center;
			align-items: center;
			transition: 0.1s ease-in;

			&.active {
				background-color: rgba(255, 255, 255, 0.199);
			}
			&:hover {
				background-color: rgba(255, 255, 255, 0.296);
			}
		}
	}
}
